<template>
  <div class="auth">
    <div class="editor-popup auth-pane">
      <h1 class="editor-popup__title">Sign In</h1>
      <form style="width: 100%;" v-on:submit.prevent="login">
        <div class="field">
          <label class="label" for="email">Email address</label>
          <input type="email" class="input input--outline" id="email" v-model="email" />
        </div>

        <div class="field">
          <label class="label" for="password">Password</label>
          <input type="password" class="input input--outline" id="password" v-model="password" />
        </div>
        <div class="field">
          <button class="button button--fluid button--info" @click="login">Log In</button>
        </div>

        <div class="field" style="margin: 80px  0 40px;">
          <router-link
            to="/reset"
            style="align-self: center; color:#00bfff; font-size: 22px; text-decoration: none;"
          >Forgot Password?</router-link>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import api from "../../../api";

export default {
  name: "basic-page",
  data() {
    return {
      email: null,
      password: null
    };
  },
  methods: {
    login: function() {
      api.auth
        .login({ email: this.email, password: this.password })
        .then(result => {
          if (result.success) {
            this.$router.push({ name: "Projects" });
          }
        });
    }
  }
};
</script>

