export default {
  state: {
    popupActivities: {
      addMark: false,
      addMedia: false,
    },
  },
  getters: {},
  mutations: {
    openPopup(state, payload) {
      console.log(payload);
      state.popupActivities[payload] = true;
    },
    closePopup(state, payload) {
      state.popupActivities[payload] = false;
    },
  },
};
