<template>
  <div class="container">
    <div class="dashboard columns scene-editor">
      <div class="column column-55">
        <div
          v-if="!isLoading"
          :class="{ columnBody: true, isHome: selectedScene.type === 'home', dropzone: true, subsceneEdit: selectedScene && selectedScene.type === 'subscene' }"
          ref="refDropzone"
        >
          <div v-if="isLoading">
            <p>Loading...</p>
          </div>
          <div v-if="!isLoading && selectedScene && getSceneBigImage">
            <img
              class="scene-img"
              :style="selectedScene.type === 'subscene' ? { width: '650px' } : {} "
              :src="getSceneBigImage"
            />
          </div>
          <div
            class="scene-marks"
            v-if="!isLoading && getSelectedScene.type === 'subscene' && getSelectedScene.marks.length"
          >
            <DraggableMark
              v-for="mark in getSelectedScene.marks"
              :draggable="true"
              :key="mark.name"
              :mark="mark"
            />
          </div>
          <div v-if="selectedProject && scenes.length > 0" class="assigned-scenes">
            <DraggableScene v-for="scene in scenes" :key="scene.id" :scene="scene" />
          </div>
        </div>
        <SceneInfo v-if="selectedProject && getSelectedScene" :scene="getSelectedScene" />
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
import { initDropzone, initDraggable } from "../../utils";

import DraggableScene from "../../components/Dashboard/DraggableScene";
import DraggableMark from "../../components/Dashboard/DraggableMark";
import AddMarkPopup from "../../components/Dashboard/Popups/AddMark";
import AddMediaPopup from "../../components/Dashboard/Popups/AddMedia";
import SceneInfo from '../../components/Dashboard/SceneInfo';

import api from "../../api";

export default {
  data() {
    return {
      isLoading: true
    };
  },
  computed: {
    ...mapState([
      "selectedScene",
      "selectedProject",
      "lang"
    ]),
    ...mapGetters(["getSelectedScene", "getSelectedProject"]),
    getSceneBigImage() {
      return this.isLoading ? null : this.selectedScene.imageBig;
    },
    scenes() {
      if (this.selectedScene.id === "home") {
        return this.getSelectedProject.scenes.filter(s => s.type === "scene");
      } else if (
        this.selectedScene.type === "scene" &&
        this.selectedScene.subscenes.length > 0
      ) {
        return this.getSelectedScene.subscenes;
      } else {
        return [];
      }
    }
  },
  methods: {
    ...mapMutations([
      "setSelectedProject",
      "setSelectedScene",
      "changeScene",
    ])
  },
  mounted: function() {
    if (!this.selectedProject && !this.selectedScene) {
      this.isLoading = true;

      api.projects.loadProjectById(this.$route.params.id).then(project => {
        const scenes = project.scenes.filter(scene => scene.type === "scene");
        const home = project.scenes.find(scene => scene.type === "home");
        const subscenes = project.scenes
          .filter(scene => scene.subscenes && scene.subscenes.length)
          .map(scenes => scenes.subscenes);

        const scene = [home, ...scenes, ...subscenes.flat()].find(
          v => v.id === this.$route.params.sid
        );

        this.setSelectedProject(project);
        this.setSelectedScene(scene);

        this.isLoading = false;
      });
    }

    initDropzone(".dropzone", this, false, element => {
      const id = element.getAttribute("data-id");
      const x = element.getAttribute("data-x");
      const y = element.getAttribute("data-y");

      this.changeScene({
        id,
        update: {
          pos: {
            x,
            y
          }
        }
      });

      initDraggable(element, ev => {
        // eslint-disable-next-line
        console.log(ev);
      });
    });
  },
  components: {
    DraggableScene,
    DraggableMark,
    SceneInfo
  }
};
</script>

<style lang="scss">
.scene-img {
  border: 1px solid rgba(255, 255, 255, 0.2);
}

.assigned-scenes {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.scene-editor {
  overflow: auto;

  .column-55 {
    width: auto;
  }

  .column-body {
    position: relative;
    max-width: 100%;
    min-width: 1920px;
    height: 845px;
    border: 1px solid #ddd;

    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.subsceneEdit {
  max-width: none;
  min-width: auto;
}

.columnBody.dropzone {
  position: relative;
  border: 1px solid #ddd;

  &.isHome {
    min-height: 1080px;
    width: 1920px;
  }
}

.scene-marks {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
</style>
