export default {
  state: {
    newMark: null,
  },
  getters: {
  },
  mutations: {
    createMark(state, payload) {
      state.newMark = payload;
    },
    addMark(state, payload) {
      const mark = {
        ...state.newMark,
        ...payload,
      };

      state.selectedScene.marks.push(mark);
      state.popupActivities.addMark = false;
      state.popupActivities.newMark = null;
    },
  },
};
