import Vue from 'vue';

export default {
  state: {
    selectedScene: null,
  },
  getters: {
    getSelectedScene: (state) => state.selectedScene,
  },
  mutations: {
    setSelectedScene(state, payload) {
      state.selectedScene = payload;
    },
    changeScene(state, payload) {
      const { id, update } = payload;
      const indexOfScene = state.selectedProject.scenes.map((e) => e.id).indexOf(id);
      let updatedScene = state.selectedProject.scenes.find((scene) => scene.id === id);

      updatedScene = {
        ...updatedScene,
        ...update,
      };

      Vue.set(state.selectedProject.scenes, indexOfScene, { ...updatedScene });
    },
    saveScene(state, payload) {},
  },
};
