import Vue from 'vue';
import Vuex from 'vuex';

import tiles from './store/tiles';
import projects from './store/projects';
import scenes from './store/scenes';
import marks from './store/marks';
import editor from './store/editor/index';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    ...tiles.state,
    ...projects.state,
    ...scenes.state,
    ...marks.state,
    ...editor.state,
  },

  getters: {
    ...tiles.getters,
    ...projects.getters,
    ...scenes.getters,
    ...marks.getters,
    ...editor.getters,
  },

  mutations: {
    ...tiles.mutations,
    ...projects.mutations,
    ...scenes.mutations,
    ...marks.mutations,
    ...editor.mutations,
  },
});
