<template>
  <div class="auth">
    <div class="editor-popup auth-pane">
      <h1 class="editor-popup__title">Sign Up</h1>
      <form style="width: 100%;" v-on:submit.prevent="login">
        <div class="field">
          <label class="label" for="name">First and last name</label>
          <input type="text" class="input input--outline" id="name" v-model="name" />
        </div>
        <div class="field">
          <label class="label" for="email">Company</label>
          <input type="text" class="input input--outline" id="company" v-model="company" />
        </div>
        <div class="field">
          <label class="label" for="email">Address</label>
          <input type="text" class="input input--outline" id="address" v-model="address" />
        </div>
        <div class="field">
          <label class="label" for="email">Phone number</label>
          <input type="email" class="input input--outline" id="email" v-model="phone" />
        </div>
        <div class="field">
          <label class="label" for="email">Email address</label>
          <input type="email" class="input input--outline" id="email" v-model="email" />
        </div>
        <div class="field">
          <label class="label" for="password">Password</label>
          <input type="password" class="input input--outline" id="password" v-model="password" />
        </div>
        <div class="field">
          <button class="button button--fluid button--info">Sign Up</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import api from "../../../api";

export default {
  name: "basic-page",
  data() {
    return {
      email: null,
      name: null,
      password: null,
      company: null,
      address: null,
      phone: null
    };
  },
  methods: {
    login: function() {
      api.auth
        .register({
          name: this.name,
          company: this.company,
          address: this.address,
          phone: this.phone,
          email: this.email,
          password: this.password
        })
        .then(result => {
          if (result.success) {
            this.$router.push({ name: "Projects" });
          }
        });
    }
  }
};
</script>

