<template>
  <header class="header">
    <div class="header__tile project-icon"></div>
    <div class="header__center">
      <h1 class="header__project-name">{{title}}</h1>
      <div>
        <LangSwitcher />
      </div>
    </div>
    <div class="header__tile menu-icon">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </header>
</template>

<script>
import LangSwitcher from "./UI/LangSwitcher";
import { mapState } from 'vuex';

export default {
  components: {
    LangSwitcher
  },
  computed: {
    ...mapState(['selectedProject', 'selectedScene']),
    title () {
      if (this.selectedScene) {
        return this.selectedScene.name;
      }

      if (this.selectedProject) {
        return this.selectedProject.name;
      }

      return 'Projects';
    }
  }
};
</script>

