<template>
  <div class="editor-popup">
    <div class="editor-popup__content">
      <div class="editor-popup__content-type">
        <img width="32" :src="require(`../../../assets/marks/${newMark.type}.svg`)" />
        <h3>Type: {{newMark.type}}</h3>
      </div>
      <input placeholder="Label EN" v-model="label.en" />
      <input placeholder="Link DE" v-model="link.en" />
      <br />
      <input placeholder="Label DE" v-model="label.de" />
      <input placeholder="Link DE" v-model="link.de" />
      <br />
      <button @click="add">add</button>
      <button @click="close">close</button>
    </div>
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  data() {
    return {
      label: {
        en: "",
        de: ""
      },
      link: {
        en: "",
        de: ""
      }
    };
  },
  computed: {
    ...mapState(["newMark"])
  },
  methods: {
    ...mapMutations(["addMark", "closePopup"]),
    add() {
      this.addMark({
        label: this.label,
        link: this.link
      });
    },
    close() {
      this.closePopup("addMark");
    }
  }
};
</script>

