<template>
  <div :class="`mark ${mark.handleNext ? 'mark--next' : ''} ${mark.isNew ? 'mark--new' : ''}`">
    <div
      :class="`mark__flag mark__flag--${mark.type}`"
      :style="{left: `${mark.x}%`, top: `${mark.y}%`}"
      @click="handleClick"
    >
      <span v-if="mark.isNew" class="mark__icon">
        <img :src="require(`../assets/marks/${mark.type}.svg`)" />
      </span>
      <span class="mark__text" v-html="!mark.isNew ? mark.title[lang] : mark.label[lang]" />
    </div>
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
      <div class="overlay" v-if="showPopup"></div>
    </transition>
    <transition enter-active-class="animated flippInY" leave-active-class="animated flippOutY">
      <Popup v-if="showPopup" :data="mark" @close-popup="showPopup=false" />
    </transition>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import Popup from "@/components/Popup";

export default {
  name: "mark-item",

  data() {
    return {
      showPopup: false
    };
  },

  methods: {
    handleClick: function(e) {
      if (this.mark.isNew) {
        const win = window.open(this.mark.link[this.lang], "_blank");
        win.focus();
      }

      if (this.mark.handleNext) {
        this.fullScreen();
      }
    },

    ...mapMutations(["setScene"]),
    fullScreen() {
      this.setScene({
        scene: this.mark.handleNext,
        type: "sub"
      });
    }
  },

  props: {
    mark: Object
  },

  computed: {
    ...mapState(["lang"])
  },

  components: {
    Popup
  }
};
</script>

