<template>
  <div :class="{sidebar: true, sidebarLeft: true, sidebarActive: sidebarActivities.media }">
    <VerticalExpander text="Media" type="media" />

    <div class="sidebar__content" v-if="sidebarActivities.media">
      <div class="title title--small">Project Media</div>
      <div class="sidebar__actions">
        <button class="button button--add" @click="handleAddMediaClick">Add Media</button>
      </div>
      <div class="media__content">
        <div v-if="getMedia.length">
          <div v-for="mediaBlock in getMedia" :key="mediaBlock.title">
            <div v-if="mediaBlock.data.length">
              <h3 class="media__conent_header">{{mediaBlock.title}}</h3>
              <MediaItem v-for="media in mediaBlock.data" :key="media.id" :media="media" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters, mapMutations } from "vuex";

import VerticalExpander from "../UI/VerticalExpander";
import MediaItem from "../MediaItem";

export default {
  computed: {
    ...mapState(["sidebarActivities"]),
    ...mapGetters(["getSelectedProject"]),
    getMedia() {
      const { media } = this.getSelectedProject;

      return [
        {
          title: "PDF",
          data: media.filter(item => item.type === "pdf")
        },
        {
          title: "LINK",
          data: media.filter(item => item.type === "link")
        },
        {
          title: "AUDIO",
          data: media.filter(item => item.type === "audio")
        },
        {
          title: "VIDEO",
          data: media.filter(item => item.type === "video")
        }
      ];
    }
  },

  methods: {
    ...mapMutations(['openPopup']),
    handleAddMediaClick() {
      this.openPopup('addMedia');
    }
  }
,
  components: {
    VerticalExpander,
    MediaItem
  }
};
</script>

<style lang="scss" scoped>
.media__conent_header {
  color: #4c4c4c;
  font-size: 28px;
}
</style>
