<template>
  <div :class="['scene', `scene--${type}`, `scene--${flag}`]">
    <!-- home section [start] -->
    <transition
      enter-active-class="animated fadeIn"
      leave-active-class="animated fadeOut"
      mode="out-in"
    >
      <div v-if="currentScene === 'home'">
        <div class="scene__icon" @click="fullScreen" key="button">
          <div class="scene__img">
            <slot name="image" />
          </div>
          <div :class="`scene__flag scene__flag--${flag} flag`" v-html="sceneData.title[lang]" />
        </div>
      </div>
    </transition>
    <!-- home section [end] -->

    <!-- 1-st level tile [start] -->
    <transition
      enter-active-class="animated zoomIn"
      leave-active-class="animated zoomOut"
      mode="out-in"
    >
      <div class="scene__full" v-if="type === currentScene && !subScene">
        <div class="scene__full-img">
          <div class="image-full">
            <slot name="imageFull" />
          </div>
          <!-- <div class="scene__subscene" v-if="sceneData.subscenes && sceneData.subscenes.length">
            <SceneSub
              v-for="scene in sceneData.subscenes"
              class="subscene"
              :key="scene.id"
              :scene="scene"
            />
          </div>-->
          <MarkItem
            v-for="mark in sceneData.marks"
            class="mark"
            :key="mark.type + mark.x + mark.y"
            :mark="mark"
          />
        </div>
      </div>
    </transition>
    <!-- 1-st level tile [end] -->

    <!-- 2-nd level tile [end] -->
    <transition
      enter-active-class="animated zoomIn"
      leave-active-class="animated zoomOut"
      mode="out-in"
    >
      <div class="subscene scene__full" v-if="type === currentScene && subScene">
        <div class="subscene scene__full-img">
          <img :src="sceneData.imageSmall" />
          <MarkItem
            v-for="mark in sceneData.marks"
            class="mark"
            :key="!mark.isNew ? mark.title[lang] + mark.x + mark.y : mark.label[lang] + mark.x + mark.y"
            :mark="mark"
          />
        </div>
      </div>
    </transition>
    <!-- 2-nd level tile [end] -->
  </div>
</template>

<script>
import { mapMutations, mapState } from "vuex";
import MarkItem from "@/components/MarkItem";
import SceneSub from "@/components/SceneSub";
import scenes from "@/data";

export default {
  name: "scene-full",

  props: {
    subSceneType: String,
    type: String,
    flag: String
  },

  computed: {
    ...mapState(["lang", "currentScene", "subScene"]),
    sceneData() {
      let scene = scenes.find(scene => scene.id === this.type);

      if (this.subScene) {
        scene = scene.subscenes.find(scene => scene.id === this.subScene);
      }

      return { ...scene };
    }
  },

  methods: {
    ...mapMutations(["setScene"]),
    fullScreen() {
      this.setScene({
        scene: this.type
      });
    }
  },

  components: {
    MarkItem
  }
};
</script>

