<template>
  <div
    class="mark"
    ref="draggableMark"
    :style="{left: `${mark.x}%`, top: `${mark.y}%`}"
    @click="onClick"
  >
    <img :src="require(`../../assets/marks/${mark.name || mark.type}.svg`)" class="mark__image" />
    <span class="mark__text" v-html="!mark.isNew ? mark.title[lang] : mark.label[lang]" />
  </div>
</template>

<script>
import { initDraggable } from "../../utils";
import { mapState } from "vuex";
import { setTimeout } from "timers";

export default {
  data() {
    return {
      x: 0,
      y: 0
    };
  },
  computed: {
    ...mapState(["lang"])
  },
  props: {
    mark: Object,
    draggable: Boolean,
    handleClick: Function
  },
  methods: {
    onClick() {
      if (this.handleClick) {
        this.handleClick();
      }
    }
  },
  mounted() {
    if (this.draggable) {
      const { draggableMark } = this.$refs;

      setTimeout(() => {
        const bounds = draggableMark.getBoundingClientRect();
        console.log(bounds);
        // const x = bounds.x - bounds.width;
        // const y = bounds.y - bounds.height;

        draggableMark.setAttribute("data-x", bounds.x);
        draggableMark.setAttribute("data-y", bounds.y);
      }, 100);

      initDraggable(draggableMark, () => {
        console.log("dragging");
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.mark {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  min-height: 64px;
  word-wrap: break-word;
  text-align: left;
  font-size: 16px;
  font-weight: 500;
  white-space: nowrap;
  z-index: 7;
  background-color: #7a9cb5;
  color: #fff;
  padding: 23px 35px 15px 40px;
  min-width: auto;

  &__text {
    display: inline-block;
  }

  &__image {
    position: absolute;
    height: 100%;
    min-height: 64px;
    min-width: 64px;
    z-index: 1;
    object-fit: contain;
    left: -32px;
    top: 0;
  }
}
</style>

