<template>
  <div class="projects">
    <div v-if="isLoading">
      <p>Loading...</p>
    </div>

    <div v-for="project in projects" :key="project.id" class="project">
      <div class="project__name">{{ project.name }}</div>
      <div class="project__preview">
        <img :src="project.preview" />
      </div>
      <div class="project__actions">
        <div class="project__action project__action--left">
          <button class="button button--icon">
            <img :src="require('../../assets/editor/bin-icon.svg')" />
          </button>
        </div>
        <div class="project__action project__action--right">
          <button class="button" @click="goToProject(project.id)">Edit</button>
          <button class="button">Preview</button>
          <button class="button">Publish</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";

import api from "../../api";
import fake from "../../fake";

export default {
  data() {
    return {
      isLoading: true
    };
  },

  computed: {
    ...mapState(["projects"]),
    ...mapGetters(["getProjects"])
  },

  mounted() {
    if (!this.projects.length) {
      this.loadProjects();
    } else {
      this.isLoading = false;
    }
  },

  methods: {
    ...mapMutations(["setProjects", "setSelectedProject"]),
    loadProjects: function() {
      api.projects.loadProjects("user_id").then(projects => {
        this.setProjects({ projects });
        this.isLoading = false;
      });
    },
    goToProject: function(id) {
      const project = this.projects.find(el => el.id === id);

      this.setSelectedProject(project);
      this.$router.push({ path: `project/${id}` });
    }
  }
};
</script>

