export default (BASE_URL) => ({
  login: function(credentials) {
    // eslint-disable-next-line
    console.log(BASE_URL, credentials);

    return new Promise((res, rej) => {
      localStorage.setItem('jwt_token', JSON.stringify(credentials)); // change on jwt token
      localStorage.setItem('login_date', Date.now().toString());

      res({ success: true });
    });
  },
  register: function(credentials) {
    return new Promise((res, rej) => {
      localStorage.setItem('jwt_token', JSON.stringify(credentials)); // change on jwt token
      localStorage.setItem('login_date', Date.now().toString());

      res({ success: true });
    });
  },
  reset: function(email) {
    return new Promise((res, rej) => {
      alert(`reset pass for ${email}`);
    });
  },
  logout: function() {
    localStorage.removeItem('jwt_token');
    localStorage.removeItem('login_date');
  },
  isAuthenticated: function() {
    const loginDate = localStorage.getItem('login_date');
    const jwtToken = localStorage.getItem('jwt_token');

    if (loginDate === null || jwtToken === null) {
      this.logout();

      return false;
    }

    const now = Date.now();
    const delta = 1000 * 60 * 60 * 23; // 23 hours

    if (now - loginDate < delta) {
      return true;
    }

    return false;
  },
});
