import fake from '../../fake';

export default (BASE_URL) => ({
  loadProjects: function(userId) {
    // /api/:userId/projects
    return new Promise((res, rej) => {
      setTimeout(() => {
        res(fake.projects);
      }, 1000);
    });
  },
  // /api/:userId/projects/:projectId
  loadProjectById: function (projectId) {
    return new Promise((res, rej) => {
      setTimeout(() => {
        const project = fake.projects.find(val => val.id === projectId);

        res(project);
      }, 1000);
    });
  }
});
