import Vue from 'vue';
import VueRouter from 'vue-router';

import App from './App.vue';

import store from './store';
import router from './routes';

Vue.config.productionTip = false;

Vue.use(VueRouter);

const app = new Vue({
  router,
  store,
  render: (createEle) => createEle(App),
}).$mount('#app-container');
