<template>
  <div
    :class="{
    page: true,
    graphic: true,
    overlayed: showInformation,
    large: large,
    home: isHomepage
  }"
    ref="page"
    v-bind:style="{ transform: `scale(${scale})`,
      maxWidth: large && isMaxWidth ? `${widthMax}` : null,
      minWidth: isMinWidth ? `${widthMin}` : null,
    }"
  >
    <transition enter-active-class="animated fadeIn" leave-active-class="animated fadeOut">
      <img v-if="currentScene === 'home'" class="page__bg" src="../assets/bg-map.jpg" alt />
    </transition>

    <div class="page__inner">
      <header class="page__header">
        <div class="page__info">
          <transition
            name="slide"
            enter-class="slide-enter"
            enter-active-class="animated slideInLeft"
            leave-active-class="animated fadeOut"
            mode="out-in"
          >
            <h1
              class="page__title"
              v-if="true"
              v-html="currentTitle[lang]"
              :key="currentTitle['de'] + currentScene"
            />
          </transition>
          <transition
            name="slide"
            enter-active-class="animated slideInLeft"
            leave-active-class="animated fadeOut"
            mode="out-in"
          >
            <div
              class="page__subtitle"
              v-if="true"
              v-html="currentSubtitle[lang]"
              :key="currentSubtitle['de'] + currentScene"
            />
          </transition>
          <div class="page__information-pane" v-if="currentInformation">
            <transition
              name="slide"
              enter-active-class="animated slideInLeft"
              leave-active-class="animated fadeOut"
              mode="out-in"
            >
              <button
                class="page__info--button"
                v-if="showInformation === false && isHomepage"
                :key="currentSubtitle['de'] + currentScene"
                @click="showInformation = true"
              >{{ lang === 'en' ? "More Information" : "Mehr Erfahren" }}</button>
            </transition>

            <transition
              name="slide"
              enter-active-class="animated slideInLeft"
              leave-active-class="animated fadeOut"
              mode="out-in"
            >
              <div
                class="page__information-content"
                v-if="showInformation || !isHomepage"
                :key="currentInformation.title['de'] + currentScene"
              >
                <div class="page__information-content__inner">
                  <div
                    class="page__information-content__title"
                    v-html="currentInformation.title[lang]"
                  ></div>
                  <div class="page__information-content__body">
                    <div v-html="currentInformation.text[lang]" />
                    <button v-if="isHomepage" @click="showInformation = false" class="close" />
                  </div>
                  <div class="page__information-content__links">
                    <a
                      v-for="link in currentInformation.links[lang]"
                      :href="link.url"
                      :key="link.url"
                    >{{link.text}}</a>
                  </div>
                </div>
              </div>
            </transition>
          </div>
        </div>
        <img
          :src="logoSrc"
          class="page__logo"
          @click="setScene({
            scene: 'home',
            type: null
          });"
        />
      </header>
      <div class="page__scenes">
        <div class="page__row">
          <SouverentyScene class="page__scene" type="souverenty" flag="right" />
        </div>
        <div class="page__row">
          <InteroperabilityScene class="page__scene" type="interoperability" flag="left" />
          <SustainabilityScene class="page__scene" type="sustainability" flag="right" />
        </div>
      </div>
      <footer class="page__footer">
        <div
          class="page__back-btn"
          @click="setHomeScreen"
          v-if="currentScene!=='home'"
        >{{ btnText[lang] }}</div>
        <div class="page__lang">
          <LangSwitcher />
        </div>
      </footer>

      <SocialShare />

      <transition
        name="fade-in-overlay"
        enter-active-class="animated"
        leave-active-class="animated"
        mode="out-in"
      >
        <div v-if="showInformation" class="overlay" />
      </transition>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";
import LangSwitcher from "@/components/LangSwitcher";
import MainLogo from "@/assets/logo.svg";
import SustainabilityScene from "@/scenes/SustainabilityScene";
import InteroperabilityScene from "@/scenes/InteroperabilityScene";
import SouverentyScene from "@/scenes/SouverentyScene";
import SocialShare from "@/components/SocialShare";

import data from "@/data";

export default {
  name: "basic-page",
  data() {
    return {
      scenes: data.filter(s => s.id !== "home"),
      showInformation: false,
      scale: 1,
      large: false,
      widthMax: "auto",
      widthMin: "auto",
      isMinWidth: false,
      isMaxWidth: false,
      btnText: {
        en: "Back",
        de: "Zurück"
      },
      codeText: {
        en: "More info:",
        de: "Mehr info:"
      }
    };
  },

  computed: {
    ...mapState(["lang", "currentScene", "subScene"]),
    ...mapGetters(["currentTitle", "currentSubtitle", "currentInformation"]),
    logoSrc() {
      return MainLogo;
    },
    currentSceneObj() {
      return data.find(s => s.id === this.currentScene);
    },
    isHomepage() {
      return this.currentScene === "home";
    },
    scenes_t() {
      if (this.isHomepage) {
        return data.filter(s => s.id !== "home");
      } else {
        const selectedScene = data.find(s => s.id === this.currentScene);

        return selectedScene.subscenes && selectedScene.subscenes.length
          ? selectedScene.subscenes
          : [];
      }
    }
  },

  methods: {
    ...mapMutations(["setScene"]),
    setHomeScreen() {
      this.setScene({
        scene: this.subScene ? this.currentScene : "home",
        type: null
      });
    }
  },
  mounted() {
    this.setHomeScreen();
    this.scale =
      window.innerWidth > 1024 ? (window.innerWidth / 1000) * 0.5207 : 0.534528;
    this.large = window.innerWidth > 1920 ? true : false;

    this.widthMax = 1920 + "px";
    if (window.innerWidth > 1920 && window.innerHeight > 1080) {
      if (1080 * this.scale > window.innerHeight) {
        this.isMaxWidth = true;
        this.scale = (window.innerHeight / 1000) * 0.9255;
        this.widthMax = window.innerWidth / this.scale + "px";
      }
    } else {
      this.widthMax = "auto";
      if (
        this.$refs.page.scrollHeight * this.scale > window.innerHeight &&
        window.innerWidth > 1024
      ) {
        this.isMinWidth = true;
        this.scale = (window.innerHeight / 1000) * 0.9255;
        this.widthMin = window.innerWidth / this.scale + "px";
      }
    }

    window.onresize = event => {
      if (window.innerWidth > 1024) {
        this.scale = (window.innerWidth / 1000) * 0.5207;
      }

      this.large = window.innerWidth > 1920 ? true : false;

      this.isMaxWidth = false;
      this.isMinWidth = false;
      this.widthMax = 1920 + "px";
      if (window.innerWidth > 1920 && window.innerHeight > 1080) {
        if (1080 * this.scale > window.innerHeight) {
          this.isMaxWidth = true;
          this.scale = (window.innerHeight / 1000) * 0.9255;
          this.widthMax = window.innerWidth / this.scale + "px";
        }
      } else {
        this.widthMax = "auto";
        if (
          this.$refs.page.scrollHeight * this.scale > window.innerHeight &&
          window.innerWidth > 1024
        ) {
          this.isMinWidth = true;
          this.scale = (window.innerHeight / 1000) * 0.9255;
          this.widthMin = window.innerWidth / this.scale + "px";
        }
      }
    };
  },

  components: {
    LangSwitcher,
    SustainabilityScene,
    InteroperabilityScene,
    SouverentyScene,
    SocialShare
  }
};
</script>

