<template>
  <div>
    <div v-if="isLoading">
      <p>Loading...</p>
    </div>

    <div class="scenes" v-if="selectedProject">
      <div
        v-for="scene in scenesRender"
        :key="scene.id"
        :class="['scene', { 'no-margin': type === 'scene'} ]"
      >
        <img
          ref="myDraggable"
          v-if="draggable"
          draggable="true"
          :src="scene.imageSmall"
          class="drag-drop draggable-image"
          :data-id="scene.id"
        />
        <div class="scene__name">
          <span>{{ scene.name }}</span>
          <button
            :class="`button ${scene.type === 'home' ? 'button--info' : ''}`"
          >Startscreen</button>
        </div>
        <div class="scene__preview">
          <img :src="scene.imageSmall" />
        </div>
        <div class="scene__actions" v-if="type === 'all' || !type">
          <div class="scene__action scene__action--left">
            <button class="button button--icon">
              <img :src="require('../../assets/editor/bin-icon.svg')" />
            </button>
          </div>
          <div class="scene__action scene__action--right">
            <button class="button" @click="goToScene(scene.id)">Edit</button>
            <button class="button" @click="saveScene(scene.id)">Save</button>
          </div>
        </div>
      </div>
      <div class="scene scene--new" @click="addNewScene" v-if="type === 'all' || !type">
        <h2>Add new scene</h2>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations, mapGetters } from "vuex";

import { initDraggable } from "../../utils";
import api from "../../api";
import fake from "../../fake";

export default {
  props: {
    type: String,
    draggable: Boolean,
    showUsed: Boolean
  },

  data() {
    return {
      isLoading: false
    };
  },

  computed: {
    ...mapState(["selectedProject", "selectedScene"]),
    scenesRender() {
      if (this.isLoading || !this.selectedProject) return [];

      let result = null;

      if (!this.type || this.type === "all") {
        const scenes = this.selectedProject.scenes.filter(
          scene => scene.type === "scene"
        );
        const home = this.selectedProject.scenes.find(
          scene => scene.type === "home"
        );
        const subscenes = this.selectedProject.scenes
          .filter(scene => scene.subscenes && scene.subscenes.length)
          .map(scenes => scenes.subscenes);

        result = [home, ...scenes, ...subscenes.flat()];
      }

      if (this.type === "scene") {
        result = this.selectedProject.scenes.filter(
          scene => scene.type === this.type
        );
      } else if (this.type === "subscene") {
        result = this.selectedProject.scenes
          .filter(scene => scene.subscenes && scene.subscenes.length)
          .map(scenes => scenes.subscenes);
      }

      return result;
    }
  },

  mounted() {
    if (!this.selectedProjecot) {
      this.isLoading = true;

      api.projects.loadProjectById(this.$route.params.id).then(project => {
        this.setSelectedProject(project);
        this.isLoading = false;
      });
    }

    if (this.draggable) {
      const draggables = this.$refs.myDraggable;

      if (draggables.length) {
        draggables.forEach(draggableItem =>
          initDraggable(draggableItem, event => {
            // eslint-disable-next-line
            console.log(event);
          })
        );
      }
    }
  },

  methods: {
    ...mapMutations(["setSelectedScene", "setSelectedProject"]),
    goToScene: function(id) {
      const scene = this.scenesRender.find(el => el.id === id);
      this.setSelectedScene(scene);
      console.log();
      this.$router.push({
        name: "Scene",
        params: {
          id: this.$route.params.id,
          sid: id
        }
      });
    },
    saveScene: function() {
      // eslint-disable-next-line
      console.log("save");
    },
    addNewScene: function() {
      // eslint-disable-next-line
      console.log("add new");
    }
  }
};
</script>

